/*--------------------cart-page__left----------------------*/
.praongallery {
  box-sizing: border-box;
}
.praongallery * {
  box-sizing: border-box;
}

.praongallery__slider.slick-dotted {
  margin-bottom: -7px;
}
.praongallery__slider .slick-prev,
.praongallery__slider .slick-next {
  width: 32px;
  height: 32px;
  top: 190px;
}
.praongallery__slider .slick-prev {
  left: -30px;
}
.praongallery__slider .slick-next {
  right: -30px;
}
.praongallery__slider .slick-prev:before, .praongallery__slider .slick-next:before {
  content: "";
  width: 32px;
  height: 32px;
  display: block;
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
}
.praongallery__slider .slick-next:before {
  background-image: url(../img/static/knportal_base/img/a-next.png);
}
.praongallery__slider .slick-prev:before {
  background-image: url(../img/static/knportal_base/img/a-prev.png);
}
.praongallery__slider .slick-dots {
  margin-top: 14px;
  position: static;
  display: flex;
  margin-left: -3.5px;
  margin-right: -3.5px;
}
.praongallery__slider .slick-dots li {
  width: auto;
  height: auto;
  margin: 0;
  display: block;
  margin-left: 3.4px;
  margin-right: 3.4px;
  margin-bottom: 7px;
}
.praongallery__slider .slick-dots li a {
  width: 98px;
  height: 80px;
  display: block;
}
.praongallery__slider .slick-dots li a img {
  width: 100%;
  cursor: pointer;
  object-fit: cover;
  object-position: center;
  height: 100%;
  border-radius: 5px;
}

.praongallery__slide {
  height: 379px;
  text-align: center;
}
.praongallery__slide img {
  max-height: 100%;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

/*--------------------END cart-page__left----------------------*/
.header .sidebar-menu {
  display: flex;
}
.header .sidebar-menu .dl-submenu {
  opacity: 0;
  visibility: hidden;
}

/*--------------------_right-sidebar----------------------*/
.right-info .author div {
