  text-align: center;
  box-sizing: border-box;
  min-width: 180px;
  background-color: rgba(0, 42, 147, 0.7);
  color: #fff;
  font-weight: 900;
}

.wrap-new-main__second-box ul li {
  margin-top: 0;
}
.wrap-new-main__second-box ul li:not(:last-child) {
  margin-bottom: 10px;
}
.wrap-new-main__second-box ul li a {
  color: #3E3E3E;
}

.map-toggle {
  margin-top: 11px;
  font-size: 14px;
  color: #139E19;
  border: none;
  outline: none;
